import axios from 'axios';
import React, { useEffect, useState } from 'react'

const BrowseProduct = () => {
     const [products,setproducts]=useState([]);

    const fetchProducts = async () => {
      const res = await axios.get('http://localhost:5000/products/getall');
      console.log(res.status);
     console.log(res.data);
     setproducts(res.data);
    };
    useEffect(() => {
      fetchProducts();
    }, []);

      

  return (
    <div className='min-h-screen flex flex-col items-center justify-center px-4 py-8'>
      <h1 className='text-3xl font-bold mb-8'>Browse Product Page</h1>
      <div>
        <h2 className='text-2xl font-semibold mb-4'>Product Listings</h2>
        <div className='grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6'>
          {products.map(product => (
            <div key={product._id} className='bg-white rounded-xl shadow-md p-6 flex flex-col items-center'>
              <img
                src={product.images}
                alt={product.name}
                className='w-32 h-32 object-cover rounded-lg mb-4'
              />

              <h3 className='text-xl font-bold mb-2'>{product.name}</h3>
              <p className='text-gray-600 mb-1'>Brand: {product.brand}</p>
              <p className='text-gray-600 mb-1'>Category: {product.category}</p>
              <p className='text-gray-800 font-semibold mb-2'>â‚¹{product.price}</p>
              <p className='text-gray-600 mb-1'>Stock: {product.stock}</p>
              <p className='text-gray-600 mb-2'>Tags: {product.tags}</p>
              <p className='text-gray-700 text-sm'>{product.description}</p>
              {/* Add more details or actions as needed */}
            </div>
          ))}
        </div>
      </div>
      {/* Add filters and sorting options as needed */}
    </div>
  );
}

export default BrowseProduct;
